<template>
    <div class="m-header">
        <!-- 左上角定位 -->
        <div class="header-left" @click="_handLeft">
            <!-- 插槽 -->
            <slot>
                <span class="back icon-back"></span>
            </slot>
        </div>
        <!-- 中部 -->
        <div class="header-center">
            <!-- 搜索块 -->
            <form class="search" action="">
                <input type="text" placeholder="请输入想搜索的服务">
            </form>
        </div>
        <!-- 右上角消息栏 -->
        <div class="header-right">
            <!-- 消息块 -->
            <div class="message">
                <a href="/information" class="icon-message"></a>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            title: {
                type: String,
                default: '如逸悠悠'
            }
        },
        methods: {
            _handLeft() {
                this.$emit('_handHeaderLeft')
            }
        }
    }
</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable.styl"

    .m-header
        display flex
        position fixed
        top 0
        z-index 90
        width 100%
        height 0.6825rem
        line-height 0.6825rem
        background-color $color-theme
        text-align center
        .header-left
            flex 4
            padding 0 0.1875rem
            color #FFF2DF
            font-size 0.25rem
            > .icon-location:before
                margin-right 0.2em
        .header-right
            display block
            flex 2
            padding 0 0.28rem 0 0.1875rem
            font-size 0.30rem
            .message > a
                color #FFF2DF
        .header-center
            flex 20
            .search
                padding 0 0.1rem
                height 100%
                > input
                    width 100%
                    height 70%
                    border 0
                    border-radius 100px
                    font-size 0.2rem
                    text-indent 1em
                    outline none
            .title
                font-size 0.25rem
</style>



