<template>
    <div ref="wrapper">
        <slot></slot>
    </div>
</template>

<script>
    import BScroll from 'better-scroll'

    const DIRECTION_H = 'horizontal';
    const DIRECTION_V = 'vertical';

    export default {
        props: {
            probeType: {
                type: Number,
                default: 1
            },
            click: {
                type: Boolean,
                default: false
            },
            listenScroll: {
                type: Boolean,
                default: false
            },
            data: {
                type: Array,
                default: null
            },
            pullup: {
                type: Boolean,
                default: false
            },
            beforeScroll: {
                type: Boolean,
                default: false
            },
            refreshDelay: {
                type: Number,
                default: 20
            },
            direction: {
                type: Boolean,
                default: false
            }
        },
        mounted() {
            setTimeout(() => {
                this._initScroll()
            }, 20)
        },
        methods: {
            _initScroll() {
                // 检测 wrapper 层
                if (!this.$refs.wrapper) {
                    return
                }

                // 配置 Scroll
                this.scroll = new BScroll(this.$refs.wrapper, {
                    scrollX: this.direction,
                    scrollY: !this.direction,
                    probeType: this.probeType,
                    click: this.click,
                    eventPassthrough: this.direction ? DIRECTION_V : DIRECTION_H
                });

                // obj{x, y} 滚动的实时坐标
                if (this.listenScroll) {
                    this.scroll.on('scroll', (pos) => {
                        this.$emit('scroll', pos)
                    })
                }

                // obj{x, y} 滚动结束的位置坐标
                if (this.pullup) {
                    this.scroll.on('scrollEnd', () => {
                        // maxScrollX / maxScrollY  => obj 超过滚动的最大区间 50 将派发 scrollToEnd 事件
                        if (this.scroll.x <= (this.scroll.maxScrollX + 50)) {
                            this.$emit('scrollToEnd')
                        }
                        if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
                            this.$emit('scrollToEnd')
                        }
                    })
                }

                // obj 在滚动开始之前 ，会派发 beforeScroll 事件
                if (this.beforeScroll) {
                    this.scroll.on('beforeScrollStart', () => {
                        this.$emit('beforeScroll')
                    })
                }
            },
            disable() {
                this.scroll && this.scroll.disable()
            },
            enable() {
                this.scroll && this.scroll.enable()
            },
            refresh() {
                this.scroll && this.scroll.refresh()
            },
            scrollTo() {
                this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
            },
            scrollToElement() {
                this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
            }
        },
        watch: {
            data() {
                setTimeout(() => {
                    this.refresh()
                }, this.refreshDelay)
            }
        }
    }
</script>



