import TabsTopComponent from './tabs-top.vue'
import TabsBottomComponent from './tabs-bottom.vue'
import TabsLeftComponent from './tabs-left.vue'

const TabsComponents = {
    install(Vue) {
        Vue.component('tab-top', TabsTopComponent);
        Vue.component('tab-bottom', TabsBottomComponent);
        Vue.component('tab-left', TabsLeftComponent);
    }
};

export default TabsComponents



