<template>
    <div class="admin">
        <div>账号：<input type="text" v-model="user"></div>
        <div>密码：<input type="password" v-model="pw"></div>
        <div>验证码：<input type="text"></div>
        <input type="button" value="提交" @click="register">
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        data() {
            return {
                user: '',
                pw: '',
            }
        },
        methods: {
            register() {
                console.log('发送请求', `user: ${this.user}`);
                axios.post('http://www.zscycn.com/wap/passport/sendsms', {
                    mobile: this.user
                })
                    .then(function (res) {
                        console.log(res);
                    })
                    .catch(function (err) {
                        console.log(err);
                    })
            }
        }
    }
</script>

<style lang="stylus">
    .admin
        background-color #aaa
        > *
            margin-bottom 20px
</style>



