<template>
    <div id="app">
        <div v-show="loading" class="loads">
            <loading class="load"></loading>
        </div>
        <router-view/>
    </div>
</template>

<script>
    // 获取组件
    import Loading from 'base/loading/loading'

    // 兼容
    const htmlStyle = document.documentElement ? document.documentElement : document.body;

    // vue
    export default {
        data() {
            return {
                screenWidth: 750, // 初始值
                loading: true
            }
        },
        components: {
            Loading
        },
        created() {
            // 初始化页面布局 --> rem
            this._rem(htmlStyle.offsetWidth);
        },
        mounted() {
            window.onresize = () => {
                this.screenWidth = htmlStyle.offsetWidth;
            };
            setTimeout(() => {
                this.loading = false
            }, 500)
        },
        updata() {

        },
        methods: {
            _rem(v = 750) { // 动态改变 html 字体大小
                if (v >= 750) {
                    htmlStyle.style.fontSize = '117.1875px'
                } else if (v <= 320) {
                    htmlStyle.style.fontSize = '50px'
                } else {
                    htmlStyle.style.fontSize = `${v / 320 * 50}px`
                }
            }
        },
        watch: {
            screenWidth(v) { // 监听窗口大小改变
                this._rem(v);
            }
        }
    }
</script>

<style lang="stylus">
    #app
        font-family 'Avenir', Helvetica, Arial, sans-serif
        -webkit-font-smoothing antialiased
        -moz-osx-font-smoothing grayscale
        text-align center
        color #2c3e50
        width 100vw
        height 100vh
        .loads
            position fixed
            z-index 9999
            top 0
            left 0
            width 100%
            height 100%
            background-color rgba(0, 0, 0, 0.6)
            .load
                position absolute
                top 50%
                transform translate(0, -50%)
                color #fff
</style>



