<template>
    <div class="t-header">
        <!-- 左上角定位 -->
        <div class="header-left" @click="_handLeft">
            <!-- 返回块 -->
            <slot name="header-left">
                <span class="back icon-back" ref="back"></span>
            </slot>
        </div>
        <!-- 中部 -->
        <div class="header-center">
            <!-- 标题块 -->
            <slot name="header-center">
                <span class="title" style="color: #fff;">{{title}}</span>
            </slot>
        </div>
        <!-- 右上角消息栏 -->
        <div class="header-right">
            <!-- 插槽 -->
            <slot></slot>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            title: {
                type: String,
                default: '如逸悠悠'
            }
        },
        methods: {
            _handLeft() {
                this.$emit('_handHeaderLeft')
            }
        }
    }
</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable.styl"

    .t-header
        display flex
        position fixed
        top 0
        z-index 90
        width 100%
        height 0.6825rem
        line-height 0.6825rem
        background-color $color-theme
        text-align center
        .header-left
            flex 4
            padding 0 0.1875rem
            color #FFF2DF
            font-size 0.25rem
        .header-right
            display block
            flex 4
            padding 0 0.1875rem
            font-size 0.25rem
            > *
                color #FFF2DF
        .header-center
            flex 20
            .title
                font-size 0.25rem
</style>



