<template>
    <div class="modality">
        <div class="mask" @click="showMask"></div>
        <slot></slot>
    </div>
</template>

<script>
    export default {
        methods: {
            showMask() {
                this.$emit('handMask');
            }
        }
    }
</script>

<style scoped lang="stylus">
    .modality
        position fixed
        top 0
        left 0
        width 100vw
        height 100vh
        z-index 200
        > .mask
            width 100%
            height 100%
            background-color rgba(0, 0, 0, 0.33)
</style>



