<template>
    <div class="details">
        <header>
            <div class="back icon-back" @click="_back"></div>
            <div class="icon-phone"></div>
        </header>
        <div class="bg">
            <div class="user">
                <img src="~common/images/600.jpg" alt="">
                <h2>无极电脑维修</h2>
            </div>
        </div>
        <div class="info">
            <div>
                <div class="title">完成订单</div>
                <div class="good">621单</div>
            </div>
            <div>
                <div class="title">好评率</div>
                <div class="good">100%</div>
            </div>
            <div>
                <div class="title">认证系统</div>
                <div class="icon-setter"></div>
                <div class="icon-order"></div>
            </div>
            <div class="published">
                <div class="title">ta发布的任务</div>
                <ul @click="handPublished($event)">
                    <li class="active">电脑维修</li>
                    <li>重装系统</li>
                    <li>电脑清灰</li>
                    <li>配置升级</li>
                </ul>
            </div>
        </div>
        <div class="contact">
            <div>
                <div class="title icon-timer">服务时间</div>
                <time class="timer">9:00~18:00</time>
            </div>
            <div>
                <div class="title">备注：其余时间加钱联系</div>
            </div>
            <div>
                <div class="title icon-phone">联系方式</div>
                <div class="phone">1337788660</div>
            </div>
        </div>
        <div class="rmb">
            <div class="title"> 价 格 </div>
            <div class="actual">修完面谈</div>
        </div>
    </div>
</template>

<script>
    export default {
        methods: {
            handPublished(e) {
                if (!e || !e.currentTarget || e.target === e.currentTarget) {
                    console.log('诶。点击触发失败了  (。_。)');
                    return
                }
                Array.prototype.forEach.call(e.currentTarget.children, function (item) {
                    item.classList.remove('active')
                });
                e.target.classList.add('active')
            },
            _back() {
                this.$router.back()
            }
        }
    }
</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable.styl"

    .details
        width 100%
        height 100%
        > header
            position absolute
            top 0
            display flex
            justify-content space-between
            width 100%
            > div
                padding 0.25rem
                color $color-text-ll
        .bg
            display flex
            justify-content center
            align-items center
            width 100%
            height 2.56rem
            > .user
                top 0.59733rem
                > img
                    display inline-block
                    margin-bottom 0.16213rem
                    width 0.8192rem
                    height 0.8192rem
                    border 2px solid #ccc
                    border-radius 50%
                > h2
                    font-size 0.22186rem
        .info
            margin-bottom 0.17066rem
            width 100%
            > div
                display flex
                justify-content flex-start
                align-items center
                margin-bottom 2px
                padding 0.17066rem
                background-color #fff
                [class^="icon-"], [class*=" icon-"]
                    margin-right 0.19626rem
                .good
                    color $color-theme
            .published
                display block
                padding-bottom 0
                > div.title
                    text-align left
                    margin-bottom 0.256rem
                > ul
                    display flex
                    font-size 0.22186rem
                    > li
                        margin-left auto
                        margin-right auto
                        padding-bottom 0.17066rem
                        border-bottom 2px solid transparent
                        transition border-bottom-color 1s
                        &.active
                            border-bottom-color $color-theme
        .contact
            margin-bottom 0.17066rem
            width 100%
            > div
                display flex
                justify-content flex-start
                align-items center
                margin-bottom 2px
                padding 0.17066rem
                background-color #fff
                > .title
                    position relative
                    padding-left 0.34133rem
                    &:before
                        position absolute
                        left 0
                        color $color-theme
                > .timer, .phone
                    color $color-text3
                    font-size 0.18773rem
        .rmb
            display flex
            justify-content flex-start
            align-items center
            margin-bottom 2px
            padding 0.17066rem
            width 100%
            background-color #fff
            .actual
                color $color-text3
                font-size 0.18773rem
        .title
            min-width 0.93866rem
            margin-right 0.17066rem
            text-align left
            font-size 0.22186rem
            font-weight 600
</style>



