<template>
    <div class="setter">
        <t-header :title="'个人资料'" @_handHeaderLeft="_back"></t-header>
        <ul class="setter-list">
            <li>
                <a data-id="avatar"
                   class="icon-right"
                   href="javascript:;"
                   @click="handModality($event)">
                    <span>头像</span>
                    <div class="avatar"><img src="~common/images/600.jpg" alt=""></div>
                </a>
            </li>
            <li>
                <a data-id="gender"
                   class="icon-right"
                   href="javascript:;"
                   @click="handModality($event)">
                    <span>性别</span>
                    <span class="old">男</span>
                </a>
            </li>
            <li>
                <router-link class="icon-right" to="setter/name">
                    <span>用户名</span>
                    <span class="old">老用户名</span>
                </router-link>
            </li>
            <li>
                <router-link class="icon-right" to="setter/phone">
                    <span>手机号</span>
                    <span class="old">12345678901</span>
                </router-link>
            </li>
            <li>
                <router-link class="icon-right" to="about/setter/name">
                    <span>钱包绑定</span>
                    <span class="old"></span>
                </router-link>
            </li>
        </ul>
        <mask-modal v-if="showMask" v-on:handMask="removeMask">
            <div class="modality-list">
                <h3>{{Modality.title}}</h3>
                <div :class="`icon-${Modality.icon[0]}`">{{Modality.options[0]}}</div>
                <div :class="`icon-${Modality.icon[1]}`">{{Modality.options[1]}}</div>
            </div>
        </mask-modal>
    </div>
</template>

<script>

    export default {
        data() {
            return {
                Modality: {
                    type: '',
                    title: '',
                    icon: ['', ''],
                    options: ['', '']
                },
                showMask: false
            };
        },
        methods: {
            handModality(e) {
                let el = e.currentTarget.getAttribute('data-id');
                if (el == 'avatar') {
                    this.Modality = {
                        type: 'avatar',
                        title: '修改头像',
                        icon: ['pen', 'pen'],
                        options: ['拍照', '相册']
                    }
                }
                if (el == 'gender') {
                    this.Modality = {
                        show: true,
                        type: 'gender',
                        title: '修改性别',
                        icon: ['pen', 'pen'],
                        options: ['男', '女']
                    }
                }
                this.showMask = !this.showMask;
            },
            removeMask(data) {
                this.showMask = !this.showMask
            },
            _back() {
                this.$router.back()
            }
        }
    }

</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable.styl"

    .setter-list
        padding-top 0.6825rem
        > li
            margin-bottom 0.05rem
            > a
                position relative
                display flex
                align-items center
                justify-content space-between
                padding 0.17rem
                background-color #fff
                > *
                    margin 0 0.1rem
                .old
                    margin 0 0.3rem
            .icon-right:before
                position absolute
                right 0
                margin 0 0.1rem
            .avatar
                margin 0 0.3rem
                border 0
                border-radius 50%
                overflow hidden
                > img
                    width 0.7rem
                    height 0.7rem

    .modality-list
        position absolute
        top 30%
        padding 0.38rem
        width 100%
        text-align left
        > h3
            margin-bottom 2px
            padding 0.22rem 0.35rem
            font-size 0.22rem
            text-align left
            background-color #fff
            border-radius 0.5em
        > div
            margin-bottom 2px
            padding 0.18rem 0.4rem
            font-size 0.18rem
            text-align left
            background-color #fff
            border-radius 0.3em
            &:before
                position relative
                top 0.0125rem
                left -0.1rem
                font-size 0.25rem
                color $color-theme

</style>



