<template>
    <div class="about">
        <div class="user-bg">
            <div class="user">
                <div class="avatar icon-pen">
                    <img src="~common/images/600.jpg" alt="">
                </div>
                <div class="name">用户名</div>
            </div>
        </div>
        <div class="about-options">
            <ul class="option-top">
                <li>
                    <span class="icon-timer"></span>
                    <span>全部</span>
                </li>
                <li>
                    <span class="icon-timer"></span>
                    <span>待接单</span>
                </li>
                <li>
                    <span class="icon-timer"></span>
                    <span>已接单</span>
                </li>
                <li>
                    <span class="icon-timer"></span>
                    <span>评价</span>
                </li>
            </ul>
            <ul class="option-bottom">
                <li>
                    <a href="about/coupon">
                        <span class="icon-timer"></span>
                        <span>领券中心</span>
                        <span class="icon-right"></span>
                    </a>
                </li>
                <li>
                    <a href="about/card">
                        <span class="icon-timer"></span>
                        <span>卡券包</span>
                        <span class="icon-right"></span>
                    </a>
                </li>
                <li>
                    <a href="about/integral">
                        <span class="icon-timer"></span>
                        <span>积分中心</span>
                        <span class="icon-right"></span>
                    </a>
                </li>
                <li>
                    <a href="about/friends">
                        <span class="icon-timer"></span>
                        <span>邀请好友</span>
                        <span class="icon-right"></span>
                    </a>
                </li>
                <li>
                    <a href="about/alipay">
                        <span class="icon-timer"></span>
                        <span>支付宝绑定</span>
                        <span class="icon-right"></span>
                    </a>
                </li>
                <li>
                    <a href="about/share-reward">
                        <span class="icon-timer"></span>
                        <span>分享有奖</span>
                        <span class="icon-right"></span>
                    </a>
                </li>
                <li>
                    <a href="about/collection">
                        <span class="icon-timer"></span>
                        <span>我的收藏</span>
                        <span class="icon-right"></span>
                    </a>
                </li>
            </ul>
        </div>
        <div class="about-setter">
            <a href="about/setter" style="color: #fff;" class="icon-setter"></a>
        </div>
        <tab-bottom></tab-bottom>
    </div>
</template>

<script>
    export default {}
</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable.styl"

    .about
        position relative
        .user-bg
            position relative
            height 2.56rem
            background #ccc
            .user
                position absolute
                left 50%
                top 40%
                transform translate(-50%, -50%)
                .avatar
                    position relative
                    width 1.024rem
                    height 1.024rem
                    margin-bottom 0.2rem
                    > img
                        display block
                        width 1.024rem
                        height 1.024rem
                        border 0
                        border-radius 50%
                    &:before
                        display block
                        position absolute
                        right 0
                        bottom 0
                        width 0.22185rem
                        height 0.22185rem
                        line-height 0.22185rem
                        color #fff
                        background-color $color-theme
                        font-size 0.13rem
                        text-align center
                        border 0.025rem solid #fff
                        border-radius 50%
                .name
                    text-indent 0.25em
                    font-size 0.23rem
                    text-align center
        .about-options
            position relative
            padding 0 3.5%
            transform translate(0, -0.512rem)
            > ul
                margin-bottom 0.185rem
                background-color #fff
            .option-top
                display flex
                text-align center
                border 0
                border-radius 0.128rem
                > li
                    flex 1
                    padding 0.213333rem
                    font-size 0.213333rem
                    > span:first-child
                        display block
                        margin-bottom 0.12rem
                        height 0.35rem
                        line-height 0.35rem
                        font-size 0.3rem
                        color $color-theme
            .option-bottom
                background-color transparent
                > li
                    margin-bottom 1px
                    padding 0.18rem
                    border 0
                    border-radius 0.128rem
                    background-color #fff
                    text-align left
                    font-size 0.213333rem
                    > a
                        display block
                        color $color-text
                        span
                            margin 0 0.1rem
                        .icon-right
                            float right
                        &:last-child
                            margin-bottom 0
        > .about-setter
            position fixed
            top 0
            right 0
            padding 0.25rem
</style>



