<template>
    <div class="slider" ref="slider">
        <div class="slider-group" ref="sliderGroup">
            <slot></slot>
        </div>
        <div class="dots" v-show="MiniDot">
            <span class="dot"
                  :class="{active: currentPageIndex === index }"
                  v-for="(item, index) in dots">
            </span>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import {addClass} from 'common/js/dom'
    import BScroll from 'better-scroll'

    export default {
        name: 'slider',
        props: {
            loop: { // 无缝轮播
                type: Boolean,
                default: true
            },
            autoPlay: { // 紫铜
                type: Boolean,
                default: true
            },
            interval: {
                type: Number,
                default: 4000
            },
            MiniDot: {
                type: Boolean,
                default: false
            },
            pageIndex: {
                type: Number,
                default: 0
            }
        },
        data() {
            return {
                dots: [],
                currentPageIndex: 0
            }
        },
        mounted() {
            // 初始化 slider 组件
            setTimeout(() => {
                this._setSliderWidth();
                this._initDots();
                this._initSlider();
                // 判断 是否开启轮播
                if (this.autoPlay) {
                    // 开启轮播 则执行轮播动画
                    this._play()
                }
            }, 20);

            // 窗口改变时初始化组件大小
            window.addEventListener('resize', () => {
                // scroll 是否正常启动
                if (!this.slider || !this.slider.enabled) {
                    return
                }

                // 清除定时器
                clearTimeout(this.resizeTimer);
                // 设置定时器
                this.resizeTimer = setTimeout(() => {
                    // 判断当前 scroll 是否处于滚动动画的过程
                    if (this.slider.isInTransition) {
                        // true 调用 _onScrollEnd
                        this._onScrollEnd()
                    } else {
                        // false 判断是否开启滚动动画
                        if (this.autoPlay) {
                            // 如果开启滚动动画，则再次执行动画
                            this._play()
                        }
                    }
                    // 重新计算 dom 结构
                    this.refresh()
                }, 60)
            })
        },
        activated() {
            // 启动 scroll
            this.slider.enable();

            // 获取滚动页面数
            let pageIndex = this.slider.getCurrentPage().pageX;

            // 判断是否超出滚动最大限制  && 无缝轮播
            if (pageIndex > this.dots.length) {
                // 是否超出 滚动限制，如果 超出，将回滚到 1
                pageIndex = pageIndex % this.dots.length
            }

            // 滚动到指定页面
            this.slider.goToPage(pageIndex, 0, 0);

            // 是否开启 无缝轮播
            if (this.loop) {
                // 如果开启 循环滚动，会复制首尾，这里需要 -1
                pageIndex -= 1
            }

            // py
            this.currentPageIndex = pageIndex;

            // 是否开启轮播，开启轮播会在滚动结束再次调用
            if (this.autoPlay) {
                this._play()
            }
        },
        deactivated() {
            // 当组件被停用时 ，禁用 scroll
            this.slider.disable();
            // 清除定时器
            clearTimeout(this.timer)
        },
        beforeDestroy() {
            // 实例销毁之间清除 scroll 影响
            this.slider.disable();
            // 清除定时器
            clearTimeout(this.timer)
        },
        methods: {
            refresh() {
                // init
                if (this.slider) {
                    this._setSliderWidth(true);
                    this.slider.refresh()
                }
            },
            _setSliderWidth(isResize) {
                // 获取 子项
                this.children = this.$refs.sliderGroup.children;
                // 宽度
                let width = 0;
                // slider 宽度
                let sliderWidth = this.$refs.slider.clientWidth;
                // 循环添加类名，计算宽度
                for (let i = 0; i < this.children.length; i++) {
                    let child = this.children[i];
                    addClass(child, 'slider-item');

                    child.style.width = sliderWidth + 'px';
                    width += sliderWidth
                }
                // 是否开启 无缝轮播
                if (this.loop && !isResize) {
                    // 开启无缝轮播会复制首尾
                    width += 2 * sliderWidth
                }
                // 添加宽度
                this.$refs.sliderGroup.style.width = width + 'px'
            },
            _initSlider() {
                // 实例化 scroll 对象
                this.slider = new BScroll(this.$refs.slider, {
                    scrollX: true,
                    scrollY: false,
                    momentum: false,
                    snap: {
                        loop: this.loop,
                        threshold: 0.3,
                        speed: 400
                    }
                });

                // 监听: 当滚动结束时，执行 _onScrollEnd 事件
                this.slider.on('scrollEnd', this._onScrollEnd);

                // 当 手指离开 时，再次开启自动播放
                this.slider.on('touchend', () => {
                    if (this.autoPlay) {
                        this._play()
                    }
                });

                // 滚动开始前 清除上次设置的定时器
                this.slider.on('beforeScrollStart', () => {
                    if (this.autoPlay) {
                        clearTimeout(this.timer)
                    }
                })
            },
            _onScrollEnd() {
                // 滚动结束时 获取滚动 页面
                let pageIndex = this.slider.getCurrentPage().pageX;
                // 是否 开启 无缝轮播
                if (this.loop) {
                    pageIndex -= 1
                }
                // 记录索引
                this.currentPageIndex = pageIndex;
                // 自动轮播
                if (this.autoPlay) {
                    this._play()
                }
                this.$emit('scrollToEnd', this.currentPageIndex)
            },
            _initDots() {
                // 记录 子项总长度
                this.dots = new Array(this.children.length)
            },
            _play() {
                // 轮播
                let pageIndex = this.slider.getCurrentPage().pageX + 1;
                // 清除上一个定时器
                clearTimeout(this.timer);
                // 开启定时器
                this.timer = setTimeout(() => {
                    // 滚动到下一页
                    this.slider.goToPage(pageIndex, 0, 400)
                }, this.interval)
            },
            _listMove(i) {
                this.slider.goToPage(i, 0, 2000);
            }
        },
        watch: {
            pageIndex: "_listMove"
        }
    }
</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable"

    .slider
        min-height 1px
        .slider-group
            position relative
            overflow hidden
            white-space nowrap
            height 100%
            .slider-item
                float left
                box-sizing border-box
                overflow hidden
                text-align center
                height 100%
                a
                    display block
                    width 100%
                    overflow hidden
                    text-decoration none
                img
                    display block
                    width 100%
        .dots
            position absolute
            right 0
            left 0
            bottom 12px
            transform translateZ(1px)
            text-align center
            font-size 0
            .dot
                display inline-block
                margin 0 4px
                width 8px
                height 8px
                border-radius 50%
                background $color-text-l
                &.active
                    width 20px
                    border-radius 5px
                    background $color-text-ll
</style>



