<script>
    import Slider from 'base/slider/slider'

    export default {
        data() {
            return {}
        },
        components: {
            Slider
        },
        methods: {
            _handProductList(event) {
                let items = this.$refs.productList.children;
                Array.prototype.forEach.call(items, function (item, v) {
                    item.index = v;
                    item.classList.remove('active-product-list')
                });
                event.target.classList.add('active-product-list');
                this.$refs.productListContent.style.transform = `translate(0, -${event.target.index * 6.666666666666667}%)`

            },
            _back() {
                this.$router.back()
            }
        }
    };
</script>

