<template>
    <div>
        <t-header :title="'修改用户名'" @_handHeaderLeft="_back"></t-header>
        <div id="modify" class="modify">
            <form class="icon-delete" action="">
                <input type="text">
            </form>
            <span>注意：不能使用敏感词汇，否者修改失败</span>
            <a href="javascript:;" class="btn">保存</a>
        </div>
    </div>
</template>

<script>

    export default {
        methods: {
            _back() {
                this.$router.back()
            }
        }
    }

</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable.styl"

    .modify
        padding 0.6825rem 5% 0
        text-align left
        > form
            margin-top 0.384rem
            margin-bottom 0.2rem
            position relative
            &:before
                display block
                position absolute
                top 50%
                right 0
                padding 0.05rem
                font-size 0.2rem
                color #fff
                background-color rgba(188, 188, 188, 0.66)
                border 0
                border-radius 50%
                transform translate(0, -60%)
            > input
                width 100%
                height 0.512rem
                background-color transparent
                text-indent 0.2em
                outline none
                border-bottom 1px solid $color-theme
        > span
            color $color-text2
            font-size 0.215rem
        > a.btn
            display block
            margin-top 0.2816rem
            width 100%
            color #fff
            font-size 0.22rem
            background-color $color-theme
            border-radius 0.7em
</style>



