import Vue from 'vue'
import Router from 'vue-router'

// 获取组件
// home
import Home from 'components/home/home'

// find
import Find from 'components/find/find'

// release
import Release from 'components/release/release'

// information
import Information from 'components/information/information'

// about
import About from 'components/about/about'
import Setter from 'components/about/setter'
import ModifyName from 'components/about/modify-name'
import Coupon from 'components/about/coupon'

// list
import ProductList from 'components/list/product-list'
import ProductItem from 'components/list/product-item'
import Details from 'components/details/details'

// admin
import admin from 'base/admin/admin'

Vue.use(Router);

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            redirect: '/home'
        },
        {
            path: '/home',
            name: 'home page',
            component: Home
        },
        {
            path: '/find',
            name: 'find page',
            component: Find
        },
        {
            path: '/release',
            name: 'release page',
            component: Release
        },
        {
            path: '/information',
            name: 'information page',
            component: Information
        },
        {
            path: '/about',
            name: 'about page',
            component: About
        },
        {
            path: '/about/setter',
            name: 'about setter page',
            component: Setter
        },
        {
            path: '/about/setter/name',
            component: ModifyName
        },
        {
            path: '/about/setter/phone',
            component: ModifyName
        },
        {
            "path": '/about/coupon',
            component: Coupon
        },
        {
            "path": '/product-list',
            component: ProductList
        },
        {
            "path": '/product-list/:id',
            component: ProductItem
        },
        {
            "path": '/product-list/:id/details',
            component: Details
        },
        {
            "path": '/admin',
            component: admin
        }
    ]
})



