<template>
    <div class="tabs">
        <router-link to="/home"><span class="icon-home">首页</span></router-link>
        <router-link to="/find"><span class="icon-location">发现</span></router-link>
        <router-link to="/release" class="tabs-find"><span class="icon-find">发布</span></router-link>
        <router-link to="/information"><span class="icon-message">消息</span></router-link>
        <router-link to="/about"><span class="icon-about">我的</span></router-link>
    </div>
</template>

<script>
    export default {}
</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable.styl"

    .tabs
        display flex
        position fixed
        bottom 0
        z-index 90
        width 100%
        height 0.8192rem
        background-color #fff
        box-shadow 0 0 0.075rem rgba(239, 239, 239, 0.66)
        > *
            flex 1
            text-align center
            padding-top 0.11rem
            > span
                position relative
                font-size 0.1792rem
                padding-top 0.0768rem
                &:before
                    display block
                    font-size 0.341333rem

    .tabs-find
        padding-top 0.432rem

    .icon-find:after
        content ''
        position absolute
        left 50%
        width 0.546133rem
        height 0.546133rem
        font-size 0.546133rem
        color #fff
        background-color $color-theme
        border 0.0512rem solid #fff
        border-radius 50%
        transform translate(-50%, -100%)
        box-shadow 0 0 0.075rem rgba(239, 239, 239, 0.66)

    .icon-find:before
        position absolute
        left 50%
        z-index 100
        color #fff
        font-size 0.546133rem
        transform translate(-50%, -160%)
</style>



