// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui'
import App from './App'
import router from './router'

// 自定义全局组件
import MaskModal from 'base/mask/index'
import HeaderComponent from 'base/header/index' // m-header 是带 search 的头部组件 ， t-header 是带 title 的头部组件
import TabsComponents from 'base/tabs/index'

Vue.use(MaskModal);
Vue.use(HeaderComponent);
Vue.use(TabsComponents);

// 引入 element-ui 样式文件
import 'element-ui/lib/theme-chalk/index.css'

// 引入全局样式文件
import 'common/stylus/index.styl'

// 载入 element-ui 组件
Vue.use(ElementUI);

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    render: h => h(App)
});



