<template>
    <div class="find">
        <t-header :title="'发现'" @_handHeaderLeft="_back"></t-header>
        <tab-bottom></tab-bottom>
    </div>
</template>

<script>
    export default {
        methods: {
            _back() {
                this.$router.back()
            }
        }
    }
</script>

<style scoped lang="stylus">


    .find
        padding-top 0.6825rem
        padding-bottom 0.8192rem
</style>



