<template>
    <div class="tab">
        <div class="tab-header">
            <scroll class="list-wrapper"
                    :direction="true"
                    :probeType="3"
                    :listenScroll="true"
                    @scroll="_barScroll"
                    ref="wrapper">
                <slot name="tab-list"></slot>
            </scroll>
            <div class="list-bar" style="transform: translateX(-0px)" ref="bar"></div>
        </div>
        <div class="content">
            <slider class="content-wrapper"
                    :click="true"
                    :loop="false"
                    :autoPlay="false"
                    :pageIndex="index"
                    @scrollToEnd="_sliderMove"
                    ref="contentWrapper">
                <slot name="tab-content"></slot>
            </slider>
        </div>
    </div>
</template>

<script>
    import Scroll from 'base/scroll/scroll'
    import Slider from 'base/slider/slider'

    export default {
        data() {
            return {
                index: 0
            }
        },
        components: {
            Scroll,
            Slider
        },
        mounted() {
            // 获取列表项
            let list = this.$refs.wrapper.$el.children[0];

            //  list 列表宽度
            let listsWidth = 0;

            // 累加 子项 宽度，同时 为 content 子项附加宽度
            Array.prototype.forEach.call(list.children, function (item, v) {
                listsWidth += item.offsetWidth;
            });

            // list 宽度为 list 所有子项之和
            list.style.width = `${listsWidth + list.children.length / 2}px`;

            // 监听 点击事件
            list.addEventListener('click', e => {
                this._active(e)
            });

            // 初始化 _barMove(v)
            this._barMove(this.index)
        },
        methods: {
            _active(e) {
                // 检测 wrapper 层
                if (!this.$refs.wrapper.$el) {
                    return
                }

                // 获取 list
                let list = this.$refs.wrapper.$el.children[0];
                // 获取 每一个选项
                let items = list.children;

                // 当前点击对象索引
                let objIndex = null;

                // 添加标记
                Array.prototype.forEach.call(items, function (item, v) {
                    item.index = v
                });

                // 判断点击位置  -> 找寻 被点击的 item
                if (e.target.index) {
                    objIndex = e.target.index
                } else {
                    // 循环遍历找寻 "被点击的 item" 的索引
                    Array.prototype.forEach.call(e.path, function (item) {
                        if (typeof item.index === 'number') {
                            objIndex = item.index;
                        }
                    });
                }

                // 判断点击对象合法性
                if (typeof objIndex !== 'number') {
                    console.log('你好像碰到了什么奇怪的东西 (°ー°〃)',
                        e.target.index);
                    return
                }


                // py
                this.index = objIndex;

            },
            _barMove(v) {
                // 检测 wrapper 层
                if (!this.$refs.wrapper.$el && this.$refs.bar) {
                    return
                }

                // 获取 list
                let list = this.$refs.wrapper.$el.children[0];
                // 获取 每一个选项
                let items = list.children;

                // 排他 移除所有 active 类名
                Array.prototype.forEach.call(items, function (item) {
                    // 移除 active 类名
                    item.classList.remove('active');
                });
                // 设定 active
                items[v].classList.add('active');

                // 获取 被点击对象 的 left 值和 width 值
                this.$refs.bar.style.left = `${items[v].offsetLeft}px`;
                this.$refs.bar.style.width = `${items[v].offsetWidth}px`;

                if (items[v].children) {
                    let itemChild = 0;
                    Array.prototype.forEach.call(items[v].children, function (item) {
                        itemChild += item.offsetWidth
                    });
                    this.$refs.bar.style.left = `${items[v].children[0].offsetLeft}px`;
                    this.$refs.bar.style.width = `${itemChild}px`;
                }


            },
            _barScroll(data) {
                this.$refs.bar.style.transform = `translateX(${Math.abs(data.x) > 1 ? data.x : 0}px)`
            },
            _sliderMove(i) {
                this.index = i;
            }
        },
        watch: {
            index: "_barMove"
        }
    }
</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable.styl"

    .tab
        width 100%
        height 100%
        .tab-header
            position absolute
            z-index 1
            width 100%
            height 0.6825rem
            line-height 0.6825rem
            overflow hidden
            background-color #fff
            border-bottom 0
            box-shadow 0 0 1px $color-text2
        .list-wrapper
            width 100%
            height 100%
            .list
                height 100%
                > *
                    float left
                    padding 0 0.17rem
                    font-size 0.2304rem
                    transition color 1s
        .list-bar
            position absolute
            bottom 0
            left 0
            width 0
            height 0.0512rem
            background-color $color-theme
            transform translateX(0)
            transition width 1s, left 1s
        .content
            padding-top 0.6825rem
            height 100%
            overflow hidden
        .content-wrapper
            position relative
            height 100%
</style>



