<template>
    <div class="tabs-left">
        <!-- 左侧列表栏 -->
        <scroll class="tab-left" :click="true">
            <slot name="tab-left"></slot>
        </scroll>
        <!-- 右侧内容栏 -->
        <div class="tab-right">
            <slot name="tab-right"></slot>
        </div>
    </div>
</template>

<script>
    // 导入 scroll 组件
    import Scroll from 'base/scroll/scroll'

    export default {
        components: {
            Scroll
        }
    }
</script>

<style scoped lang="stylus">
    .tabs-left
        position relative
        height 100%
        .tab-left
            position absolute
            top 0
            left 0
            height 100%
            width 1.2rem
            background-color #fff
        .tab-right
            margin-left 1.2rem
            height 100%
</style>



